
export async function fetchDatas() {
  const response = await fetch(
    `https://valorant-api.com/v1/agents`
  );
  const datas = await response.json();
  return datas['data'];
}

export async function fetchAgentDatas(num) {
  const response = await fetch(
    `https://valorant-api.com/v1/agents`
  );
  const datas = await response.json();
  return datas['data'][num];
}