import { useEffect, useState } from "react";
import { fetchDatas, fetchAgentDatas} from "./api";

function Header() {
  return (
    <header className="hero is-dark is-bold">
      <div className="hero-body">
        <div className="container">
          <h1 className="title">Introduction Valorant Agents</h1>
        </div>
      </div>
    </header>
  );
}

function Form(props) {
  const [agentData, setData] = useState(null);
  let agentName=[];

  useEffect(() => {
    fetchDatas().then((agentData) => {
      setData(agentData);
    });
  }, []);
  if(agentData){
    function handleSubmit(event) {
      event.preventDefault();
      const { agents } = event.target.elements;
      props.onFormSubmit(agentName.indexOf(agents.value));
    }
    for(let i =0; i<agentData.length; ++i){
      agentName.push(agentData[i]['displayName']);
    }

    //なぜかSovaが2つあるのでその削除
    delete agentName[6];
    return (
      <div>
        <form onSubmit={handleSubmit}>
          <div className="field has-addons">
            <div className="control is-expanded">
              <div className="select is-fullwidth">
                <select name="agents" defaultValue="Breach">
                  {agentName.map((name) => {
                    //display agent abilities
                    return (
                      <option value={name} key={name}>{name}</option>
                    );
                  })}
                </select>
              </div>
            </div>
            <div className="control">
              <button type="submit" className="button is-dark">
                Reload
              </button>
            </div>
          </div>
        </form>
      </div>
    );
  }else{
    return (
      <p>null</p>
    );
  }
}



function Main() {
  const [agent, setData] = useState(null);
  useEffect(() => {
    fetchAgentDatas(0).then((agent) => {
      setData(agent);
    });
  }, []);

  function reloadDatas(number) {
    fetchAgentDatas(number).then((agent) => {
      setData(agent);
    });
  }
  
  if (agent) {
    //なぜかSovaが2つあるのでその削除
    let agentAbilities = agent['abilities'];
    
    return (
      //display agent icon and name
      <main>

      <Form onFormSubmit={reloadDatas} />

      
      <div className="section">
        <div className="columns is-vcentered">
          <div className="column has-text-centered">
            <figure className="image is-128x128 is-inline-block">
              <img src={agent['displayIcon']} />
            </figure>
          </div>
          <div className="column">
            <h1 className="title">
              {agent['displayName']}
            </h1>
          </div>
          <div className="column">
            <figure className="image is-128x128 is-inline-block has-background-black">
              <img src={agent['role']['displayIcon']} />
            </figure>
            <h1 className="title">{agent['role']['displayName']}</h1>
          </div>
        </div>


        <div className="columns">
          {agentAbilities.map((abilities) => {
            //display agent abilities
            return (
              <div key={abilities['displayName']} className="column" >
                {abilities['displayName']}
                <br />
                <figure className="image is-128x128 is-inline-block has-background-black">
                  <img src={abilities['displayIcon']} />
                </figure>
                <br />
                {abilities['description']}
              </div>
            );
          })}
        </div>
      </div>
      </main>
    );
  } else {
    return (
      <p>null</p>
    );
  }
}




function Footer() {
  return (
    <footer className="footer">
      <div className="content has-text-centered">
        <a href="https://valorant-api.com">Donate to Valorant API</a>
        <p>5420031 樋口佳亮 <br /> 2021年度Webプログラミング演習課題</p>
      </div>
    </footer>
  );
}

function App() {
  return (
    <div>
      <Header />
      <Main />
      <Footer />
    </div>
  );
}

export default App;